<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts }}  товар(ов)
        </span>
    </div>
    <div class="content__catalog">
      <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>
        <product-filter :price-from="filter.priceFrom" :price-to="filter.priceTo" :color-data="colorData"
                        :category-id="filter.categoryId" v-on:filter-update="filterUpdate"/>

      </aside>
      <section class="catalog">
        <div v-if="productsLoading" class="loader">Загрузка товаров...</div>
        <div v-if="productsLoadingFailing" >Произошла ошибка при загрузке товаров...
          <button @click.prevent="loadProducts">обновить</button></div>
        <product-list :productList="products"/>
        <base-pagination :count="countProducts" :per-page="productsPerPage" v-model="page"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import BasePagination from '@/components/BasePagination.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: {
    ProductList, BasePagination, ProductFilter,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,
      filter: {
        priceFrom: 0,
        priceTo: 0,
        categoryId: 0,
        color: null,
      },
      productData: null,
      productsLoading: false,
      productsLoadingFailing: false,
      colorData: null,
    };
  },
  created() {
    this.loadProducts();
    this.loadColors();
  },
  watch: {
    page() {
      this.loadProducts();
    },
  },
  methods: {
    filterUpdate(event) {
      this.filter.priceFrom = event.priceFrom;
      this.filter.priceTo = event.priceTo;
      this.filter.categoryId = event.categoryId;
      this.filter.color = event.color;
      this.loadProducts();
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailing = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filter.categoryId,
            minPrice: this.filter.priceFrom,
            maxPrice: this.filter.priceTo,
            colorId: this.filter.color,
          },
        })
          .then((response) => { this.productData = response.data; })
          .catch(() => { this.productsLoadingFailing = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/colors`)
        .then((response) => { this.colorData = response.data; });
    },
  },
  computed: {
    products() {
      return this.productData ? this.productData.items.map((product) => ({
        ...product,
        src: product.image.file.url,
      })) : [];
    },
    countProducts() {
      return this.productData ? this.productData.pagination.total : 0;
    },
  },
};
</script>
